import React from 'react';
import Day from '../week/Day'
import Aside from './Aside';
import Mainday from './Mainday';
import Main_3 from './Main_3';

function Main() {
    return (
        <main role="main" class="w980">
            <Mainday />
            <div class='main_2_Day'>        
                <h2>이달의 신규 웹툰</h2>
                { data.map(aa => (
                    <Day 
                        text = {aa.text}
                        title = {aa.title}
                        author = {aa.author}
                        img = {aa.img}
                    /> 
                )) }
            </div>
            <Aside />
            <Main_3 />
        </main>
    );
}

export default Main;


