import React from 'react'
import './Detail.css'
import { Link } from 'react-router-dom';


/* 
    링크 관련 정리
    Detail 컴포넌트 : /movie-detail로 주소창에 직접 입력할 경우 undefined임, 따라서 강제로 Home 컴포넌트로 리다이렉트 해줘야 함
    route props에 history키를 이용함
    history키 : go, goBack, goForward등으로 URL을 변경해줌, push()함수는 지정한 URL로 이동시켜줌
*/
class Detail extends React.Component {
    // 생명주기함수 = Detail 컴포넌트가 마운트되면 
    componentDidMount() {
        // 매개변수 props에 구조분해할당으로 location, history를 할당해줌
        const { location, history } = this.props;
        // 사용자가 직접 주소를 입력하면 강제로 Home으로 이동
        if(location.state === undefined) {
            history.push('/');
        }
    }
    // Day에서 값을 받아서 뿌려줌
    render() {
        const { location } = this.props;
        if(location.state) {
            return (
                <div className='detail'>
                    <p>
                        { location.state.title }
                    </p>
                    <p>
                        <img src={ location.state.img } />
                    </p>
                    <p>
                        { location.state.author }                        
                    </p>
                    <h2>
                        { location.state.text }
                    </h2>                    
                    <p>
                        <Link to="/">Close</Link>
                    </p>                    
                </div>
            )}else {
                return null;
            }
        }
}
export default Detail