import React from 'react';
import { Link } from 'react-router-dom'
import './Day.css'


function Day({text, title, author, img}) {

    return (
                <ul id='day'>
                    <li>
                        <div>
                            <Link to={{ pathname: './detail', state: { text, title, author, img }, }}>
                                <img src={img} alt="" width="150" height="120" />
                            </Link>
                        </div>
                        <h3>{title}</h3>
                        <h4>{author}</h4>
                        <p>
                            {text.slice(0,39)}...
                        </p>
                    </li>
                </ul>
    );
}

export default Day;



