import Header from './structureCombine/Header';
import Main from './structureCombine/Main';
import Footer from './structureCombine/Footer';

import React, { Component } from 'react';

class Home extends Component {
  render() {
    return (
      <div id="wrap">
        <Header />
        <Main />
        <Footer />
    </div>
    );
  }
}

export default Home;


