import React from 'react'
import {HashRouter, Route } from 'react-router-dom'
import Home from './Home';
import Home_mon from './Home_mon'
import Home_tues from './Home_tues'
import Home_wens from './Home_wens'
import Home_thur from './Home_thur'
import Home_fri from './Home_fri'
import Home_set from './Home_set'
import Home_sun from './Home_sun'
import Detail from './structureCombine/Detail';

function App() {
  return (
    <div>
      <HashRouter>
        <Route path="/" exact={true} component={Home} />
        <Route path="/mon" component={Home_mon} />
        <Route path="/tues" component={Home_tues} />
        <Route path="/wens" component={Home_wens} />
        <Route path="/tur" component={Home_thur} />
        <Route path="/fri" component={Home_fri} />
        <Route path="/set" component={Home_set} />
        <Route path="/sun" component={Home_sun} />
        <Route path="/detail" component={Detail} />
      </HashRouter>
    </div>
  );
}

export default App;