import React from 'react';
import { Link } from 'react-router-dom';

function Mainday() {
    return (
        <div class="main_1">
            <ul>
                <li className="dot_1" onclick="cc(1)"><Link to="/">요일전체</Link></li>               
                <li className="dot_1" onclick="cc(2)"><Link to="/mon">월요웹툰</Link></li>                                    
                <li className="dot_1" onclick="cc(3)"><Link to="/tues">화요웹툰</Link></li>                                        
                <li className="dot_1" onclick="cc(4)"><Link to="/wens">수요웹툰</Link></li>                    
                <li className="dot_1" onclick="cc(5)"><Link to="/tur">목요웹툰</Link></li>                                        
                <li className="dot_1" onclick="cc(6)"><Link to="/fri">금요웹툰</Link></li>                                        
                <li className="dot_1" onclick="cc(7)"><Link to="/set">토요웹툰</Link></li>                                        
                <li className="dot_1" onclick="cc(8)"><Link to="/sun">일요웹툰</Link></li>                    
            </ul>
        </div>
    );
}

export default Mainday;